html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.firstPageComp {
	background-color: #8E2DE2; /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background-image: linear-gradient(to bottom, #12375c, #8E2DE2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.firstPageSubComp{
	display: flex;
	justify-content: center;
	align-items: center;
    flex-wrap: wrap;
}

.firstPagePicture{
	border-radius : 4rem;
	margin: 2rem 5rem;
}

.firstPageTyping{
	color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 3rem;
}


.arrowDownDiv{
	display:flex;
	justify-content:center;
	align-items:flex-end;
	height:7rem;
}

.arrowDown{
	color:white;
	font-size: 4rem;
	margin: 0 1rem;
	transition: all .2s ease-in-out;
	cursor: pointer;
}

.arrowDown:hover{
	color:#f52549;
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
}


@media only screen and (max-width: 580px) {
	.arrowDownDiv{
		height:10rem;
	}
	.firstPagePicture{
		margin: 2rem 0 0 0;
	}
	.firstPageTyping{
		margin: 2rem 0 0 0;
	    font-size: 1.8rem;
	}

} 
.skillsetIcons{
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap:wrap;
} 

.skillseticon{
	height: 60px;
	margin: 1rem;
	transition: all .2s ease-in-out;
}

.skillseticon:hover{
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.skillseticonAll{
	margin: 1rem!important;
}

@media only screen and (max-width: 580px) {
	.skillseticon{
		height: 45px;
	}
	.skillseticonAll{
		height: 45px!important;
		width: 45px!important;
	}
} 
.footerIcons{
	color:white;
	font-size: 3rem;
	margin: 0 1rem;
	transition: all .2s ease-in-out;
}

.footerIcons:hover{
	color: #f52549;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
.App {
  width: 100%;
}

.barIcons{
	color:white;
	font-size: 3rem;
	margin: 0 1rem;
	transition: all .2s ease-in-out;
}

.barIcons:hover{
	color:#f52549;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

@media only screen and (max-width: 650px) {
	.barIcons{
		font-size: 2.0rem;
	}
}
